import states from './states';

const reducer = (state = states, action) => {
  const { type, payload } = action;
  let newState;
  switch (type) {
    case 'setWechatSessionId':
      newState = Object.assign({}, state, { wechatSessionId: payload });
      break;
    case 'setTwofactorInfo':
      newState = Object.assign({}, state, { twoFactorInfo: payload });
      break;
    case 'saveBindLoginInfo':
      newState = Object.assign({}, state, { bindLoginInfo: payload });
      break;
    case 'saveBindRegistrationInfo':
      newState = Object.assign({}, state, { bindRegistrationInfo: payload });
      break;
    case 'saveWechatForceBindSwitch':
      newState = Object.assign({}, state, { wechatForceBindSwitch: payload });
      break;
    default:
      newState = state;
  }
  return newState;
};

export default reducer;
