const setWechatSessionId = sessionId => {
  return {
    type: 'setWechatSessionId',
    payload: sessionId,
  };
};

const setTwofactorInfo = info => {
  return {
    type: 'setTwofactorInfo',
    payload: info,
  };
};
const saveBindLoginInfo = info => {
  return {
    type: 'saveBindLoginInfo',
    payload: info,
  };
};
const saveBindRegistrationInfo = info => {
  return {
    type: 'saveBindRegistrationInfo',
    payload: info,
  };
};
const saveWechatForceBindSwitch = info => {
  return {
    type: 'saveWechatForceBindSwitch',
    payload: info,
  };
};

export {
  setWechatSessionId,
  setTwofactorInfo,
  saveBindLoginInfo,
  saveBindRegistrationInfo,
  saveWechatForceBindSwitch,
};
