function extractApiHost(host) {
  const REG = /^(static\.)?(.+)/;
  let extractedApiHost;
  try {
    const result = host.match(REG);
    [, , extractedApiHost] = result;
  } catch (err) {
    console.error(err); // eslint-disable-line
  }
  return extractedApiHost;
}

const { protocol, hostname } = window.location;
const IS_DEV = process.env.NODE_ENV === 'development';
const DEFAULT_PRODUCTION_API_HOST = 'app.yinxiang.com';
const DEV_PAGE_URL_ORIGIN = 'https://stage-5.yinxiang.com';
const DEV_STATIC_PAGE_URL_ORIGIN = 'https://static.stage-5.yinxiang.com';
const EXTRACTED_BASE_URL = `${
  IS_DEV
    ? DEV_PAGE_URL_ORIGIN
    : `${protocol}//${extractApiHost(hostname) || DEFAULT_PRODUCTION_API_HOST}`
}`; // 当前页面去掉 static 的 baseurl 如果是 dev 环境下那就手动指定一个
const CURRENT_BASE_URL = `${
  IS_DEV ? DEV_STATIC_PAGE_URL_ORIGIN : `${protocol}//${hostname}`
}`; // 当前页面的 baseurl 如果是 dev 环境下 同样手动指定
const MARKETING_WEB_BASE_URL = `${protocol}//www.yinxiang.com`; // marketing 页面的 baseurl
const INTERFACE_BASE_URL = `${IS_DEV ? '' : `${EXTRACTED_BASE_URL}`}`; // 接口的 baseurl
const APOLLO_INTERFACE_BASE_URL = `${
  IS_DEV ? '' : EXTRACTED_BASE_URL.replace('//', '//config.')
}`; // apollo 接口的 baseurl
const EXTRACTED_DOMAIN = `${
  IS_DEV ? '' : extractApiHost(hostname) || DEFAULT_PRODUCTION_API_HOST
}`; // 纯 domain 不带协议头

export {
  protocol,
  hostname,
  EXTRACTED_BASE_URL,
  MARKETING_WEB_BASE_URL,
  CURRENT_BASE_URL,
  INTERFACE_BASE_URL,
  EXTRACTED_DOMAIN,
  APOLLO_INTERFACE_BASE_URL,
};
