import '@babel/polyfill';
import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import axios from 'axios';
import { cloneDeep } from 'lodash-es';
import qs from 'query-string';
import App from './App';
import './countly/index';
import './wx/index';
// import * as serviceWorker from './serviceWorker';
import store from './store/store';
import { processParam } from './utils/signatureUtils';
import { EXTRACTED_DOMAIN } from './config/urlBaseInfo';
import { setWechatSessionId } from './store/actions';

import 'normalize.css'; // global css reset from npm
import './index.scss';
import './assets/wechat_icon@2x.png'; // 为了 webpack 在打包时把微信图标也一起打包,暂时需要额外手动引入

if (EXTRACTED_DOMAIN) {
  document.domain = EXTRACTED_DOMAIN;
}

// axios 请求全局配置
axios.defaults.withCredentials = true;
// 请求拦截器 用于给所有需要签名的接口加签名
axios.interceptors.request.use(
  config => {
    const _config = cloneDeep(config);
    const { needSignature, method, params, data } = _config;
    if (needSignature) {
      if (method === 'post') {
        _config.data = processParam(data);
      } else if (method === 'get') {
        _config.params = processParam(params);
      }
    }
    return _config;
  },
  error => {
    console.log(error); // eslint-disable-line
    return Promise.reject(error);
  },
);

const wechatCode = qs.parse(window.location.search).code;

alert(wechatCode);

setWechatSessionId(wechatCode);

ReactDOM.render(
  <Provider store={store}>
    <App />
  </Provider>,
  document.getElementById('root'),
);

