import md5 from 'js-md5';
import { Base64 } from 'js-base64';
import { isObject } from 'lodash-es';
import {
  CONSUMER_KEY,
  CONSUMER_SECRET,
  DEVICE_ID,
} from '../config/interfaceParam';

const expandObjectToArray = object => {
  const array = [];
  const extendedObject = Object.assign(object, {
    keyPath: '',
  });
  const loopList = [extendedObject];
  while (loopList.length > 0) {
    const obj = loopList.pop();
    const { keyPath } = obj;
    delete obj.keyPath;
    Object.keys(obj).forEach(key => {
      const value = obj[key];
      const path = keyPath ? `${keyPath}.${key}` : key;
      if (isObject(value)) {
        loopList.push({
          keyPath: path,
          ...value,
        });
      } else {
        array.push({
          keyPath: path,
          value,
        });
      }
    });
  }
  return array;
};

const sortListByKey = (array, key) => {
  const { length } = array;
  if (length < 2) {
    return array;
  }
  const lArr = [];
  const rArr = [];
  const midValue = array[0];
  array.forEach(item => {
    if (item[key] < midValue[key]) {
      lArr.push(item);
    } else if (item[key] > midValue[key]) {
      rArr.push(item);
    }
  });
  return sortListByKey(lArr, key)
    .concat([midValue])
    .concat(sortListByKey(rArr, key));
};

const transformListToString = list => {
  return list.reduce((preValue, { keyPath, value }) => {
    let _preValue = preValue;
    _preValue += `${keyPath.toLowerCase()}=${value}`;
    return _preValue;
  }, '');
};

const getSignatureByParam = param => {
  const paramArray = expandObjectToArray(param);
  const sortedParamArray = sortListByKey(paramArray, 'keyPath');
  const paramString = transformListToString(sortedParamArray);
  return md5(paramString);
};

const processParam = param => {
  const {
    navigator: { userAgent },
  } = window;
  const reqAuth = {
    consumerKey: CONSUMER_KEY,
    consumerSecret: CONSUMER_SECRET,
    deviceId: DEVICE_ID,
    deviceDesc: userAgent,
    nonce: Math.ceil(Math.random() * 999999),
  };
  const extendedParam = Object.assign(param, { reqAuth });
  const signature = getSignatureByParam(extendedParam);
  const credential = Base64.encode(`${CONSUMER_KEY}:${CONSUMER_SECRET}`);
  Object.assign(extendedParam.reqAuth, { signature });
  Object.assign(extendedParam.reqAuth, { credential });
  delete extendedParam.reqAuth.consumerKey;
  delete extendedParam.reqAuth.consumerSecret;
  return extendedParam;
};

export { getSignatureByParam, processParam };
