import React from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import { getSubRoutesByPath } from './utils/routeUtils';

const routes = getSubRoutesByPath('/');

const AppRouter = () => {
  return (
    <Switch>
      {routes.map(item => (
        <Route
          path={`/${item.path}`}
          component={item.component}
          key={item.path}
        />
      ))}
      <Redirect to={`/${routes[0].path}`} />
    </Switch>
  );
};

export default AppRouter;
